    <div v-if="isVisible" class="modal-overlay" @click.self="close">
    <div class="modal-content">
        <button class="close-btn" @click="close"></button>
        <h1>{{ title }}</h1>
        <p>{{ message }}</p>
        <slot></slot> <!-- Para contedo adicional -->
    </div>
    </div>
</template>

<script>
export default {
    props: {
    title: {
        type: String,
        default: ''
    },
    message: {
        type: String,
        default: ''
    }
    },
    data() {
        return {
            isVisible: false
        };
        },
        methods: {
        open() {
            this.isVisible = true;
        },
        close() {
            this.isVisible = false;
        }
    }
};
</script>

