                  <span v-if="!isGenerating">{{ this.language == 'us-en' ? 'Share' : 'Compartilhar' }}</span>
                  <span v-else class="loading-wrapper">
                    <span class="spinner"></span>
                    {{ this.language == 'us-en' ? 'Generating...' : 'Gerando...' }}
