/* Estilo do spinner */
.spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(0,0,0,0.2);
  border-radius: 50%;
  border-top-color: currentColor; /* Usa a cor do texto */
  animation: spin 1s linear infinite;
  margin-right: 8px;
  vertical-align: middle;
}

.loading-wrapper {
  display: inline-flex;
  align-items: center;
}

/* Animao */
@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Estado desabilitado */
.disabled {
  opacity: 0.7;
  cursor: wait;
  pointer-events: none;
}

/* Garante que o link no tenha sublinhado quando desabilitado */
.nav-link.disabled {
