<template>
    <img src="data:image/svg+xml;base64," alt="SVG Icon">
    <a href="#" @click="downloadAsDoc" class="nav-link">{{ buttonText }}</a>
</template>

<script>

export default {
    computed: {
        buttonText() {
            return this.text;
        }
    },
    props: {
        text: {
            type: String,
            default: 'Download as Doc'
        }
    },
    methods: {
        async downloadAsDoc() {
            // Clona a div com o id para no modificar a original
            var contentClone = document.getElementById('template').cloneNode(true);

            // Remove todos os elementos <svg>
            var svgsToRemove = contentClone.querySelectorAll('svg');
            svgsToRemove.forEach(function (svg) {
                svg.remove();
            });

            // Remove todos os elementos <img> que no possuem a classe .img-pic
            var imgsToRemove = contentClone.querySelectorAll('img:not(.img-pic)');
            imgsToRemove.forEach(function (img) {
                img.remove();
            });


            let htmlNoneeded = contentClone.querySelectorAll('.centerImg');

            htmlNoneeded.forEach(element => {
                element.remove();
            });
            // Captura o contedo HTML modificado
            var content = contentClone.innerHTML;

            // Cria um link temporrio para fazer o download do contedo HTML como .doc
            var link = document.createElement('a');
            link.href = 'data:text/html;charset=utf-8,' + encodeURIComponent(content);
            link.download = 'document.doc';
            link.target = '_blank';

            // Simula o clique no link para iniciar o download
            link.click();
        },
    }
}
</script>

<style scoped>
.li-img {
    position: absolute;
}

.nav-link {
    background-color: #3498db;
    color: #fff;
    padding: 10px;
    border-radius: 20px;
    font-size: 11px;
}

img {
    width: 55px;
    height: 60px;
}

@media screen and (max-width: 1500px) {
    img {
        width: 35px;
        height: 35px;
    }
}
</style>