<template>
    <div class="modal-login-template">
        <div v-if="!inRequest" class="inner-login-tempalte">
            <div class="modal-header">
                <div class="modal-title">
                    <span v-if="customTitle">{{ customTitle }}</span>
                    <span v-else>{{ register ? (language === 'pt-br' ? 'Cadastro' : 'Register') : 'Login' }}</span>
                </div>
                <a v-if="!this.register" @click="this.register=true" >
                    {{ language === 'pt-br' ? 'Registrar' : 'Register'}}
                </a>
                <a v-else @click="this.register=false" >Login</a>
            </div>
            <div class="modal-body">
                <div class="form-group">
                    <input
                        id="email"
                        type="email"
                        class="form-control"
                        placeholder="email"
                        v-model="emailData"
                        @blur="validateAndEmit"
                    >
                </div>
                <div class="form-group">
                    <div v-if="!showPassword" class="pass-container">
                        <input @keydown.enter="login()" type="password" class="form-control" :placeholder="this.language === 'pt-br' ? 'Senha' : 'Password'" v-model="password" @input="validatePassword">
                        <img @click="showPassword = !showPassword" src="data:image/png;base64,">
                        <small v-if="customMessage && password.length > 0 && password.length <= 6">{{ customMessage }}</small>
                        <small v-else-if="password.length > 0 && password.length <= 6" class="text-danger">{{ this.language === 'pt-br' ? 'Sua senha deve ter pelo menos 6 caracteres.' : 'Your password must have at least 6 characters.' }}</small>
                    </div>
                    <div v-if="showPassword" class="pass-container">
                        <input type="text" class="form-control" :placeholder="this.language === 'pt-br' ? 'Senha' : 'Password'" v-model="password" @input="validatePassword">
                        <img @click="showPassword = !showPassword" src="data:image/png;base64,">
                        <small v-if="customMessage && password.length > 0 && password.length <= 6">{{ customMessage }}</small>
                        <small v-else-if="password.length > 0 && password.length <= 6" class="text-danger">{{ this.language === 'pt-br' ? 'Sua senha deve ter pelo menos 6 caracteres.' : 'Your password must have at least 6 characters.' }}</small>
                    </div>
                    <br><br v-if="register && password.length > 0">
                    <div v-if="!showConfirmPassword && register" class="pass-container">
                        <input
                            @keydown.enter="login()"
                            type="password"
                            class="form-control"
                            :placeholder="this.language === 'pt-br' ? 'Confirme sua senha' : 'Confirm your password'"
                            v-model="repeatPassword"
                            @input="validatePassword" />
                        <img @click="showConfirmPassword = !showConfirmPassword" src="data:image/png;base64,">
                        <small class="confirm-validation" v-if="!passwordMeetTheRequirements && repeatPassword.length > 0">{{ this.language === 'pt-br' ? 'As senhas devem ser iguais.' : 'The passwords must be equal.' }}</small>
                    </div>
                    <div v-if="showConfirmPassword && register" class="pass-container">
                        <div v-if="showConfirmPassword" class="pass-container">
                        <input @keydown.enter="login()" type="text" class="form-control" :placeholder="this.language === 'pt-br' ? 'Senha' : 'Password'" v-model="repeatPassword" @input="validatePassword">
                        <img @click="showConfirmPassword = !showConfirmPassword" src="data:image/png;base64,">
                        <small class="confirm-validation" v-if="!passwordMeetTheRequirements && repeatPassword.length > 0">{{ this.language === 'pt-br' ? 'As senhas devem ser iguais.' : 'The passwords must be equal.' }}</small>
                    </div>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <div class="form-group">
                    <button :disabled="!passwordMeetTheRequirements" @click="login()" type="button" class="btn btn-primary">{{ inOnboarding ? 'Confirmar' : 'Login' }}</button>
                    <button @click="cancel" type="button" class="btn btn-danger">Cancel</button>
                </div>
            </div>
        </div>
        <Loader v-else :language="language" :show="true" ></Loader>
        <!-- <div class="loading" v-else>{{ this.language === 'pt-br' ? 'Estamos verificando suas credenciais...' : 'We are checking your credentials...' }}</div> -->
    </div>
</template>
<script>
import $ from "jquery";
import Loader from "../componentesCompartilhados/Loader.vue";
export default {
    name: 'modal-login-template',
    components: {Loader},
    props: {
        email: String,
        customMessage: String,
        customTitle: String,
        language: String,
        inOnboarding: Boolean
    },
    data() {
        return {
            emailData: this.email || "",
            password: "",
            repeatPassword: "",
            passwordMeetTheRequirements: false,
            showPassword: false,
            showConfirmPassword: false,
            inRequest: false,
            register: this.inOnboarding
        }
    },
    emits: ['login', 'cancel', 'alert', 'email-updated'],
    methods: {
        login() {
            if(!this.passwordMeetTheRequirements) {
                this.$emit('alert', this.language == 'us-en' ? 'Must contain at least 7 characters' : 'Precisa ter pelo menos 7 caracteres');
                return;
            }
            this.inRequest = true;
            this.$emit('login', $('#email').val(), this.password, this.register);
        },
        cancel() {
            this.$emit('cancel');
        },
        validatePassword() {
            // Lgica para validar a senha
            if (this.password.length > 6 && this.password === this.repeatPassword) {
                this.passwordMeetTheRequirements = true;
            } else if (this.password.length > 6 && this.register == false) {
                this.passwordMeetTheRequirements = true;
            }
            else {
                this.passwordMeetTheRequirements = false;
            }
        },
        validateAndEmit() {
            const isValid = /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.emailData);
            if (isValid) {
                this.$emit('email-updated', this.emailData);
            } else {
                console.error('E-mail invlido!', this.emailData);
            }
        }
    }
}
</script>
